"use client"
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";

function LanguageSelector({ ipDataCode }) {

  const { i18n } = useTranslation();

  useEffect(() => {
    // Set the default language based on user's IP when the component mounts
    if (ipDataCode) {
      i18n.changeLanguage(ipDataCode.toLowerCase());
    }
  }, [ipDataCode, i18n]);

  const changeLanguage = (event) => {
    const selectedLanguage = event.target.value;
    i18n.changeLanguage(selectedLanguage);
  };

  const languageOptions = [
    { value: "all", label: "üåç English" },
    { value: "be", label: "üáßüá™ Belgian" },
    { value: "bg", label: "üáßüá¨ Bulgarian" },
    { value: "cz", label: "üá®üáø Czech" },
    { value: "de", label: "üá©üá™ German" },
    { value: "dk", label: "üá©üá∞ Danish" },
    { value: "es", label: "üá™üá∏ Spanish" },
    { value: "fi", label: "üá´üáÆ Finnish" },
    { value: "fr", label: "üá´üá∑ French" },
    { value: "gr", label: "üá¨üá∑ Greek" },
    { value: "hu", label: "üá≠üá∫ Hungarian" },
    { value: "it", label: "üáÆüáπ Italian" },
    { value: "nl", label: "üá≥üá± Dutch" },
    { value: "no", label: "üá≥üá¥ Norwegian" },
    { value: "pl", label: "üáµüá± Polish" },
    { value: "pt", label: "üáµüáπ Portuguese" },
    { value: "se", label: "üá∏üá™ Swedish" },
    { value: "sk", label: "üá∏üá∞ Slovak" },
    { value: "tr", label: "üáπüá∑ Turkey" },
  ];

  return (
    <select value={i18n.language} onChange={changeLanguage}>
      {languageOptions.map((option) => (
        <option key={option.value} value={option.value}>
          {option.label}
        </option>
      ))}
    </select>
  );
}

export default LanguageSelector;
